        let uiU1TextLeft = uiU1Frame.path('M12.9,77.2V4.8c0-1.5-0.2-2.5-0.7-2.8s-1.5-0.5-3.2-0.6l0-1.2h13.6v1.2h-1.3c-1.3,0-2.1,0.2-2.5,0.6c-0.4,0.4-0.5,1.2-0.5,2.6v78c0,2.8,0.9,5.1,2.7,6.9c1.8,1.8,4.1,2.7,7,2.7c3.6,0,6.4-1.1,8.1-3.4c1.8-2.3,2.7-5.7,2.7-10.3V6.2c0-1.9-0.3-3.2-0.8-3.9s-1.5-1-2.9-1H34V0.1h10.8v1.2h-1c-1.3,0-2.1,0.3-2.6,1c-0.5,0.7-0.8,2-0.8,3.9V79c0,4.9-1.1,8.5-3.4,10.9c-2.3,2.4-5.7,3.6-10.3,3.6c-3,0-5.6-0.5-7.7-1.5c-2.1-1-3.6-2.4-4.5-4.2c-0.6-1.3-1-2.6-1.2-4C13,81.6,12.9,79.4,12.9,77.2z').fill('#F8F8FB');
