        let uiIFrame = SVG(parentId).size(uiIFrameWidthPercent + 'vw', uiIFrameWidthPercent / uiIFrameRatio + 'vw').viewbox({ x: 0, y: 0, width: uiIFrameWidth, height: uiIFrameHeight * 1.026 }).attr({ 'id': 'uiIFrame' });

        let uiIText = uiIFrame.path('M14.6,0v1.2h-1.1c-1.4,0-2.3,0.2-2.8,0.6s-0.6,1.3-0.6,2.7v86.8c0,1.5,0.2,2.4,0.6,2.8s1.4,0.5,3,0.5h0.9v1.2H0v-1.2h0.7c1.6,0,2.6-0.2,3.1-0.6s0.7-1.3,0.7-2.8V4.5c0-1.5-0.2-2.4-0.6-2.8C3.6,1.4,2.6,1.3,1,1.2H0V0H14.6z').fill('#F8F8FB');
        uiIText.clipWith(uiIFrame.rect(uiIFrameWidth, 0).move(0, moveFrom));

